<template>
  <div id="analysis-wrapper">
    <div class="queryBox">
      <dataQueryBox @searchSrldId="searchSrldId" :taskList="taskList" :yearArr="yearArr"></dataQueryBox>
    </div>
    <div class="analysisBox">
      <div>
        统计维度：
        <RadioGroup type="button" v-model="analysisType" @on-change="handleChangeType">
          <Radio label="ck010">活动/设施名称</Radio>
          <Radio label="sd001">行政区划</Radio>
        </RadioGroup>
      </div>
    </div>
    <div class="statistical">
      <transition name="fade">
        <div class="sta_box" :class="{activeBox: boxstate === 1}" style="left:0;top:0;padding: 15px 5px 5px 15px">
          <div class="con_box">
            <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 :1" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 1}">
              <div class="smallBox">
                <BarChart :data="getTableListData" v-if="analysisType === 'ck010'"></BarChart>
                <BarChart2 :data="getTableListData" v-else-if="analysisType === 'sd001'"></BarChart2>
              </div>
            </div>
            <Spin size="large" fix v-if="spinShow1"></Spin>
          </div>
        </div>
      </transition>
      <div class="sta_box" :class="{activeBox: boxstate === 2}" style="left:50%;top:0;padding:15px 15px 5px 5px">
        <div class="con_box">
          <i class="stateTitle stateTitle3"></i>
          <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="handleTaleBox" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 2}">
            <div class="smallBox">
              <TableList :data="getTableListData" v-if="analysisType === 'ck010'" :heightStatus="heightStatus"></TableList>
              <TableList2 :data="getTableListData" v-else-if="analysisType === 'sd001'" :heightStatus="heightStatus"></TableList2>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow2"></Spin>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 3}" style="left:0;top:50%;padding: 5px 5px 15px 15px">
        <div class="con_box">
          <Button :icon="boxstate === 3 ? 'md-contract' : 'md-expand'" @click="mapOpen" size="small" class="boxBtn"></Button>
          <Map :boxstate="boxstate" ref="maps" @layerConClick="layerConClick" :obj="obj"></Map>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 4}" style="left:50%;top:50%;padding: 5px 15px 15px 5px">
        <div class="con_box">
          <Button :icon="boxstate === 4 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 4 ? 0 :4" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 4}">
            <div class="smallBox">
              <PieChart :data="getPieData" :title="title"></PieChart>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow3"></Spin>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dataQueryBox from './dataA_query'

import BarChart from './BarChart.vue'
import BarChart2 from './BarChart2.vue'
import BarChart3 from './BarChart3.vue'
import TableList from './TableList.vue'
import TableList2 from './TableList2.vue'
import TableList3 from './TableList3.vue'
import PieChart from './PieChart.vue'
import Map from './OnePiece'
import {
  // 任务列表列表
  listTaskSelect,
  // 统计数据
  listLedger,
  // 获取统计数字
  getNumbers
} from '@/api/dataAnalysis'
export default {
  name: 'dataAnalysis',
  data () {
    return {
      // 统计维度
      analysisType: 'ck010',
      // 任务列表
      taskList: [],
      /* 放大块的标识 */
      boxstate: 0,
      // 柱状图数据
      barChartData: {},
      // 表格数据
      getTableListData: [],
      getPieData: [],
      title: '',
      hasChecked: null, // 已核查
      haveNoChecked: null, // 未核查
      todayChecked: null, // 当日核查
      allChecked: null, // 所有问题点核查
      spinShow1: true,
      spinShow2: true,
      spinShow3: true,
      // 放大缩小状态
      heightStatus: 'small',
      // 从当前年前数5年
      yearArr: [],
      obj: {}
    }
  },
  components: {
    BarChart,
    BarChart2,
    BarChart3,
    dataQueryBox,
    TableList,
    TableList2,
    TableList3,
    PieChart,
    Map
  },
  created () {
    this.getTasks()
    this.getNumbers()
    this.getTableList()
    this.getSixYear()
  },
  methods: {
    // 从当前年前数5年
    getSixYear () {
      let dataArr = []
      let curDate = new Date()
      for (let i = 0; i < 6; i++) {
        let d = new Date(curDate.getTime() - 365 * 24 * 60 * 60 * 1000 * i)
        let preDate = d.getFullYear()
        dataArr.push({ label: preDate, value: preDate })
      }
      dataArr.reverse()
      this.yearArr = dataArr.reverse()
    },
    // 获取核查任务列表
    getTasks () {
      listTaskSelect({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.taskList.push({
              label: item.cl002,
              value: item.cl001
            })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取统计数据 */
    getTableList () {
      listLedger({
        data: {
          groupByName: this.analysisType,
          code: this.obj.adminRegionId,
          taskYear: this.obj.taskYear,
          cl001: this.obj.cl001
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableListData = []
          this.getPieData = []
          this.title = ''
          res.data.data.forEach((item, index) => {
            // 根据统计维度值显示表格数据
            if (this.analysisType === 'ck010') {
              this.spinShow1 = false
              this.spinShow2 = false
              this.spinShow3 = false
              this.title = '各斑块活动设施类型占比'
              this.getTableListData.push({
                ck010: item.ck010,
                tzNum: item.tzNum,
                trafficNum: item.emaminedNum,
                xhNum: item.unEmaminedNum,
                unxhNum: item.backNum
              })
              // 圆形统计图数据
              this.getPieData.push({
                comName: item.ck010,
                farmNum: item.tzNum
              })
            } else if (this.analysisType === 'sd001') {
              this.spinShow1 = false
              this.spinShow2 = false
              this.spinShow3 = false
              this.title = '各斑块行政区划占比'
              this.getTableListData.push({
                comName: item.sysCompany.comName,
                tzNum: item.tzNum,
                trafficNum: item.emaminedNum,
                xhNum: item.unEmaminedNum,
                unxhNum: item.backNum
              })
              // 圆形统计图数据
              this.getPieData.push({
                comName: item.sysCompany.comName,
                farmNum: item.tzNum
              })
            }
          })
          if (res.data.data.length === 0) {
            this.spinShow1 = false
            this.spinShow2 = false
            this.spinShow3 = false
            if (this.analysisType === 'ck010') {
              this.title = '各活动设施类型斑块占比'
            } else if (this.analysisType === 'sd001') {
              this.title = '各行政区划斑块占比'
            }
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    handleChangeType (val) {
      this.spinShow1 = true
      this.spinShow2 = true
      this.spinShow3 = true
      this.getTableList()
    },
    /* 级联查询返回要搜索的数据 */
    searchSrldId (obj) {
      this.obj = obj
      this.spinShow1 = true
      this.spinShow2 = true
      this.spinShow3 = true
      this.getTableList()
      this.getNumbers(this.obj.cl001)
      this.$refs.maps.getTaskList(obj)
    },
    /* 地图框放大缩小开关控制 */
    mapOpen () {
      this.boxstate = this.boxstate === 3 ? 0 : 3
    },
    /* 点击图层控制器打开地图框 */
    layerConClick () {
      this.boxstate = 3
    },
    handleTaleBox () {
      if (this.boxstate === 2) {
        this.boxstate = 0
        this.heightStatus = 'small'
      } else {
        // 放大
        this.boxstate = 2
        this.heightStatus = 'big'
      }
    },
    // 获取问题点数、核查数
    getNumbers (e) {
      if (e === null || e === undefined || e === '') {
        e = ''
      }
      getNumbers({
        data: {
          cl001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.allChecked = null
          this.hasChecked = null
          this.haveNoChecked = null
          this.todayChecked = null
          res.data.data.forEach((item, index) => {
            this.allChecked += item.checkNum
            this.hasChecked += item.checkedNum
            this.haveNoChecked += item.uncheckNum
            this.todayChecked += item.todayNum
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #analysis-wrapper{
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .analysisBox {
      text-align: left;
      padding: 0 15px;
      font-size: 14px;
      overflow: hidden;
      &>div {
        float: left;
        .ivu-radio-group {
          .ivu-radio-wrapper {
            margin-left: 10px;
            border-radius: 4px;
            &:first-child {
              margin-left: 0;
            }
            &.ivu-radio-wrapper-checked {
              background: #2d8cf0;
              color: #fff;
            }
          }
        }
      }
      &>p {
        float: right;
        span {
          margin-right: 20px;
          i {
            color: #ff0000;
            font-style: normal;
            font-size: 16px;
            font-weight: bold;
          }
        }
      }
    }
    .statistical{
      position: relative;
      width: 100%;
      height:calc(100% - 95px);
      .sta_box{
        position: absolute;
        height: 50%;
        width: 50%;
        padding:15px 20px;
        .con_box{
          height: 100%;
          width: 100%;
          background: #fff;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          overflow: hidden;
          .boxBtn{
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:calc(100vw - 60px);
            height:100%;
            .smallBox{
              width:50%;
              height:100%;
              float: left;
            }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
          }
        }
      }
      /* box放大 */
      .activeBox{
        left:0!important;
        top:0!important;
        padding: 15px !important;
        width: 100%;
        height: 100%;
        z-index: 99;
        transition: all 0.8s ease;
      }
    }
  }
</style>
